import { useDispatch, useSelector } from 'react-redux'
import { apiAddContact } from '../../redux/contacts/contactsSlice';
import { selectContactsList, selectStatus } from '../../redux/contacts/contactSlice.selectors';

import { STATUSES } from 'utils/constants';
import css from './ContactForm.module.css'
import { ButtonLoader } from 'components';


export const ContactForm = () => {
  const dispatch = useDispatch();
  const contacts = useSelector(selectContactsList);
  const status = useSelector(selectStatus)

  const onSubmit = e => {
    e.preventDefault();

    const name = e.currentTarget.elements.name.value;
    const number = e.currentTarget.elements.number.value;

    const alreadyInContacts = contacts.some(contact => contact.name.toLowerCase() === name.trim().toLowerCase())
    if (alreadyInContacts) {
      alert(`Contact ${name} is already in List.`)
      return;
    }

    const newContact = { name, number }
    dispatch(apiAddContact(newContact))

    e.currentTarget.reset();
  }

  return (
    <form className={css.form } onSubmit={onSubmit}>
      <label
        className={css.label}
        htmlFor="nameInput"
        pattern="^[a-zA-Z--]+(([' \-][a-zA-Z-- ])?[a-zA-Z--]*)*$">
        Name
      </label>
      <input className={css.input}
        type="text"
        id="nameInput"
        name="name"
        required
        placeholder="John" />               

      <label
        className={css.label}
        htmlFor="telInput">
        Number
      </label>
      <input
        className={css.input}
        type="tel"
        id="telInput"
        name="number"
        pattern="\+?\d{1,4}?[ .\-\s]?\(?\d{1,3}?\)?[ .\-\s]?\d{1,4}[ .\-\s]?\d{1,4}[ .\-\s]?\d{1,9}"
        required
        placeholder="123-45-67" />

      <button className={css.submitBtn} type="submit">
        {status === STATUSES.pending ?
          <ButtonLoader width={20} height={6} color={'white'} />
          :
          'Add Contact'}
      </button>
    </form>
  )
}